import React from 'react'
import Style from './ProductListElement.module.css'
import SmallImage from '../SmallImage/SmallImage'
import PriceList from '../PriceList/PriceList'
const ProductListElement = (props)=>{
    let styled = {
        backgroundColor:props.active?"#EFF3F6":null
    }
    // const categoryList = ()=>{
    //     let categories = []
    //         props.cat.forEach(cat=>{
    //             if( cat.subCat ){
    //                 categories = [...categories, ...cat.subCat]
    //             }
    //             categories.push(cat.name)
    //         })
    //     return(categories)
    // }
    return(
    <ul onClick={props.clicked} style={styled} className={ Style.ProductListElement }>
        <li><SmallImage addImgHandler={()=>{return null}} imgUrl={props.img} /> </li>
        <li>{ props.sku }</li>
        <li>{props.name}</li>
        <li><PriceList offprice={props.offprice} discount={props.discount} sellprice={props.sellprice} /></li>
    {/* <li>{ categoryList().map(cat=>{return(<div>{cat}</div>)}) }</li> */}
        <li>{props.attributeSet.name}</li>
        <li>{ props.units===undefined?null:Object.keys(props.units).map(size=>{return(<div key={size}>{size} - {props.units[size].unit}</div>)}) }</li>
    </ul>
    )
}

export default ProductListElement