import React, { Fragment } from 'react'
import SmallImage from '../SmallImage/SmallImage'
import Style from './OrderDetails.module.css'

const OrderDetails = (props)=>{
    let real = (    
    <div className={Style.OrderDetails}>
        <h1>Order Details</h1>
        <h3>ORDER ID : {props.order.orderId}</h3>
        <div className={Style.products}>
            {props.order.products.map(product=>{
                let productId=null
                if(props.order.isOnline){
                    productId = product
                    product = props.orderProduct[product]
                }
                return(<div className={Style.Prod}>
                        <SmallImage  imgUrl={product.productImage}/>
                        <div>
                            <div>{product.name}</div>
                            <div>₹{product.price} </div>
                            <div> Sold By: {props.order.isOnline?props.merchants[product.merchantId]:props.merchants[props.order.merchantId]} </div>
                            <div> <span> Size: {product.size} </span> <span>Qty: { product.quantity }</span> </div>
                            {props.order.isOnline?<div>Status: {product.status}</div>:null}
                        </div>
                        {props.order.isOnline?<div className={Style.SideButtons}>
                            {product.status!=="completed"&&product.status!=="cancelled"&&product.status!=="returned"?<button id="confirm" onClick={(e)=>{props.statusChangeHandler("completed",productId)}}>Confirm Fulfilment</button>:null}
                            {product.status!=="completed"&&product.status!=="cancelled"&&product.status!=="returned"?<button id={Style.cancel} onClick={(e)=>{props.statusChangeHandler("cancel",productId)}}>Cancel Product</button>:null}
                            {product.status!=="cancelled"&&product.status!=="returned"?<button onClick={(e)=>{props.statusChangeHandler("returned",productId)}}>Return Product</button>:null}
                        </div>:null}
                    </div>
                )
                })}
        </div>
            <div className={Style.total}>Total Amount: ₹{props.order.totalAmount}</div>
            <div className={Style.customer}>
                <h3>Customer Details:</h3>
                <p>Name: {props.customer?props.customer.userName:null}</p>
                <p>Phone: {props.customer?props.customer.userPhone:null}</p>
                <h2>Delivery Address:</h2>
                <p>{props.order.address}</p>
                <h1>STATUS: {props.order.status}</h1>
                {!props.order.isOnline?<div className={Style.buttons}>
                {/* {props.order.status!=="completed"?<button id="confirm" onClick={(e)=>{props.statusChangeHandler("completed")}}>Confirm Fulfilment</button>:null} */}
                {/* {props.order.status!=="completed"?<button id={Style.cancel} onClick={(e)=>{props.statusChangeHandler("cancel")}}>Cancel Order</button>:null} */}
                </div>:null}
                <button onClick={props.modifyDelivery} id="modify">Modify Delivery Address</button>
                
            </div>
        </div>
    );
    console.log(props.order.orderId)
    return(<Fragment>
        {props.order.orderId===undefined?null:real}
        </Fragment>
    )
}

export default OrderDetails