import React, {Fragment} from 'react'
import Style from './PriceList.module.css'

const PriceList = (props)=>{
    let ifselling = [<span key={0} className={Style.Selling}> ₹{props.sellprice} </span>,<span key={1} className={ Style.Real }> ₹{props.offprice} </span>]
    return(<Fragment>
        {props.discount!==0?ifselling:<span>₹{props.sellprice}</span>}
        </Fragment>
    )
}

export default PriceList