import React from 'react'
import Style from './OrderProductElement.module.css'
import SmallImage from '../../SmallImage/SmallImage'
import PriceList from '../../PriceList/PriceList'
const OrderProductElement = (props)=>{
    let product = props.product
    let merchant = props.merchants[props.merchant]
    if(props.mode){
        product=props.orderProduct[props.product]
        merchant = props.merchants[product.merchantId]
    }
    return(
        <ul className={ Style.OrderProductElement }>
        <li><SmallImage imgUrl={product.productImage} /> </li>
        <li><div>{ product.name }</div><div>Size: {product.size}</div><div>Qty: {product.quantity}</div></li>
        <li><PriceList offprice={product.offprice} sellprice={product.price} /></li>
        <li>{merchant}</li>
        <li>{props.date.toDate().toDateString()}</li>
        <li>{props.status}</li>
        <li>{props.payment}</li>
    </ul>
    )
}

export default OrderProductElement