import React from 'react';
import Style from './DropDown.module.css'
function DropDown(props) {
  let styled = {
    width:props.width?props.width:null,
    display:props.display?props.display:null,
    border:props.border?"1px solid black":null,
    margin:props.margin?props.margin:null
  }
  return (props.values && props.value!==undefined?(
    <div className={ Style.DropDown } style={styled}>
    <label htmlFor={props.name} > {props.name} </label>
    <div className={Style.label}>{props.label}</div>
      <select style={{width:props.full?"100%":null}} onChange={(e)=>{props.dropChange(props.name,e.target.value)}} value={props.value.length===0?"none":props.value[0]} name={props.name} id="cars">
          {props.values.map(val=>{
          return <option key={val.value} value={val.value}>{val.name}</option>
          })}
      </select>
  </div>
  ):props.values?(
    <div className={ Style.DropDown } style={styled}>
    <label htmlFor={props.name} > {props.name} </label>
    <div className={Style.label}>{props.label}</div>
      <select onChange={(e)=>{props.dropChange(e.target.value)}} name={props.name} id="cars" style={{width:props.full?"100%":null}}>
          {props.values.map(val=>{
          return <option key={val.value?val.value:val} value={val.value?val.value:val}>{val.name?val.name:val}</option>
          })}
      </select>
  </div>
  ):(    <div className={ Style.DropDown }>
    <label htmlFor={props.name} > {props.name} </label>
    <div className={Style.label}>{props.label}</div>
      <select style={styled} name={props.name} id="cars">
          <option value="none">None</option>
          <option value="none">None</option>
          <option value="none">none</option>
          <option value="none">none</option>
      </select>
  </div>)

  );
}

export default DropDown;
