import React from 'react';
import Style from './ListBar.module.css'
import ToolBar from './ToolBar/ToolBar'
import SubHead from './SubHead/SubHead'
import ProductListElement from '../ProductListElement/ProductListElement'
function ListBar(props) {
  return (
      <div className={ Style.ListBar }>
          <ToolBar head="Products" newProductHandler = {props.newProductHandler} searchHandler={props.searchHandler} searchString={props.searchString} sellerDropChange={props.sellerFilterChangeHandler} categoryDropChange={props.categoryFilterChangeHandler} filters={props.filters.join()} categoryList={props.allSubSubCat} merchantList={props.merchantList} placeholder="Search SKU/Barcode" />
          <SubHead />
          <div className={Style.list}>
              {props.list.map((ele,index)=>{
                let merchantIds = ele.merchantIds?[...ele.merchantIds,""]:ele.merchantIds
                let seller = merchantIds?merchantIds:ele.merchantId
                seller=seller===null?"":seller
                if( (ele.barcode.includes(props.searchString) || ele.productName.toLowerCase().includes(props.searchString.toLowerCase()) ) && seller.includes(props.filterSellerValue) && ele.subSubCategory.includes(props.filterCategoryValue)){
                    return(<ProductListElement img={ele.productImage[0]} clicked={()=>{return props.productChangeHandler(index)}} attributeSet={props.attributeSets[ele.attributeSet]} units={props.units[ele.productId]} sku={ele.barcode} name={ele.productName} offprice={ele.offerPrice} sellprice={ele.sellingPrice} discount={ele.discount} cat={ele.categories} active = {index===props.activeProduct} key={index}/> )
                }
                else{
                  return null
                }
              })}
          </div>
      </div>
  );
}

export default ListBar;
