import React, { Fragment } from 'react'
import Style from './PoductDetails.module.css'
import SmallImage from '../../component/SmallImage/SmallImage'
import InputTag from '../InputTag/InputTag'
import DropDown from '../DropDown/DropDown'
const ProductDetails = (props)=>{
    const makeVal = (val,index,label)=>{
        if(index===0){
            return <InputTag key={index} width="20%" margin="7px" height="30px" name={'cat' + index} type="text" label={label} value={val} inputChange={()=>{return null}}/>
        }
        else{
            return <InputTag key={index} width="20%" margin="7px" height="30px" name={'cat' + index} type="text" value={val}  inputChange={()=>{return null}}/>
        }
    }
    // const categoryList = ()=>{
    //     let categories = []
    //         props.product.categories.forEach(cat=>{
    //             if( cat.subCat ){
    //                 categories = [...categories, ...cat.subCat]
    //             }
    //             categories.push(cat.name)
    //         })
    //         let complete = []
    //         for(let i=0;i<categories.length;i++){
    //             complete.push({
    //                 name:categories[i],
    //                 value:categories[i]
    //             })
    //         }
    //     return(categories)
    // }
    const details = (
        <div className={Style.ProductDetails}>
        <h1>Product Details</h1>
        <div className={Style.imageList}>
            { props.product.productImage.map((img,index)=>{return(<SmallImage key={img} imgDelete={()=>{props.showConfirmation("Delete Image",props.deleteImageHandler(index))}} addImgHandler={()=>{return null}} imgUrl={img} />)}) }
            <SmallImage addImgHandler={props.addImgHandler}/>
        </div>
        <div className={Style.SoldBy}>
            <span className={Style.Bold}>Sold By: </span>{props.addprod?<DropDown width="200px" dropChange={props.addMerch} display="inline-block" values={props.merchants}/>:props.merch}
        </div>
        <div className={Style.Basic} > 
            <InputTag inputChange={props.inputChangeHandler} width="50%" type="number" height="50px" name="name" tagName="productName" label ="Product Name" value={props.product.productName}/> 
            <InputTag inputChange={props.inputChangeHandler} label="Sale Price" type="number" width="13%" height="35PX" tagName="offerPrice" name="sale" value={props.product.offerPrice}/> 
            <InputTag inputChange={props.inputChangeHandler} label="Discount" type="number" width="13%" height="35px" tagName="discount" name="discount" value={props.product.discount}/> 
            <InputTag inputChange={props.inputChangeHandler} label="Price" type="number" width="13%" height="35px" tagName="sellingPrice" name="price" value={props.product.sellingPrice}/> 
        </div>
        <div > 
            <InputTag inputChange={props.inputChangeHandler} width="100%" height="80px" name="desc" type="box" tagName="productDetail" label="Product Description" value={props.product.productDetail}/> 
        </div>
        <div className={Style.LeftAlign} > 
            <InputTag inputChange={props.inputChangeHandler} width="50%" height="30px" type="text" tagName="barcode" label="SKU" name="SKU" value={props.product.barcode}/>
            <InputTag inputChange={props.inputChangeHandler} width="40%" height="30px" type="date" label="Added On" name="addedon" value={"0/0/0"}/>
        </div>
        <div> 
            {/* {props.product.categories.length ===0?<div className={Style.Bold}>Categories</div>:categoryList().map((cat,index)=>{return makeCategory(cat,index)})} 
            {!props.addcat?(<div className={Style.add} onClick={()=>{props.showAddCat()}} > 
                <svg width="12" height="12" viewBox="0 0 25 25" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <line x1="13" x2="13" y2="25" stroke="#085076"/>
                    <line y1="12" x2="25" y2="12" stroke="#085076"/>    
                </svg> 
            </div> ):<DropDown width="100px" dropChange={props.addCat} display="inline-block" values={props.allCategories}/>} */}
            
        </div>
        {/* <div className={Style.Filters}>
            <h5 className={Style.Bold}>Filters</h5>
            <div>                
                <DropDown name="brands" value={props.product.brands} values={props.allBrands} dropChange={props.filterChangeHandler} />
                <DropDown name="colors" value={props.product.colors} values={props.allColors} dropChange={props.filterChangeHandler} />
                <DropDown name="fabrics" value={props.product.fabrics} values={props.allFabrics} dropChange={props.filterChangeHandler} />
                <DropDown name="occasions" value={props.product.occasions} values={props.allOccasions} dropChange={props.filterChangeHandler} />
            </div>
            <div>                
                <DropDown name="styles" value={props.product.styles} values={props.allStyles} dropChange={props.filterChangeHandler} />
                <DropDown name="patterns" value={props.product.patterns} values={props.allPatterns} dropChange={props.filterChangeHandler}/>
                <DropDown name="neckline" value={props.product.neckline} values={props.allNecklines} dropChange={props.filterChangeHandler}/>
                <DropDown name="Sleeves" value={props.product.sleeves} values={props.allSleeves} dropChange={props.filterChangeHandler}/>
            </div>
        </div> */}
        <div className={[Style.Basic,Style.HideLabel].join(' ')} > 
        <DropDown width="30%" dropChange={props.changeCategory} display="inline-block" values={props.categoryList} value={[props.product.category]} name="category" label="Category" full/>
        {props.product.category?<DropDown width="30%" dropChange={props.changeCategory} display="inline-block" values={props.subCategoryList[props.product.category]} value={[props.product.subCategory]} name="subCategory" label="Sub-Category" full/>:null}
        {props.product.subCategory && props.product.category?<DropDown width="30%" dropChange={props.changeCategory} display="inline-block" values={props.subSubCategoryList[props.product.category][props.product.subCategory]} value={[props.product.subSubCategory]} name="subSubCategory" label="Sub-Sub-Category" full/>:null}
        </div>
        <div className = {Style.Attributes}>
        <h5 className={Style.Bold}>Attributes</h5>
        <div className={Style.SoldBy}>
            <span className={Style.Bold}>Attribute Set:</span><DropDown width="200px" dropChange={props.attributeSetChange} display="inline-block" values={props.attributeSetsList} value={[props.product.attributeSet]} full/>
        </div>
        <span className={Style.Bold}>Attribute Values:</span>
        {props.product.attributes?props.product.attributes.map(attr=>{
            return(
                <div className={Style.AttrValues} key={attr.id}>
                    {attr.values.length ===0?<div className={Style.Bold}>{props.attributes[attr.id].name}</div>:attr.values.map((val,index)=>{return makeVal(val,index,props.attributes[attr.id].name)}) }
                    {!props.addval[attr.id]?(<div className={Style.add} onClick={()=>{props.showAddVal(attr.id)}} > 
                        <svg width="12" height="12" viewBox="0 0 25 25" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <line x1="13" x2="13" y2="25" stroke="#085076"/>
                            <line y1="12" x2="25" y2="12" stroke="#085076"/>    
                        </svg> 
                    </div> ):<DropDown width="200px" dropChange={(e)=>{props.addVal(attr.id,e)}} display="inline-block" values={['None',...props.attributes[attr.id].value]}/>}
                </div>
            )
        }):null}
        </div>
        <div>
        {props.addprod?null:<h5 className={Style.Bold}>Inverntory Information</h5>}
        <div className={Style.Inventory}>
        {props.units===undefined||props.units===null?null:Object.keys(props.units).map(size=>{return(<InputTag inputChange={props.unitChangeHandler} label={size} type="number" width="13%" height="35PX" tagName={size} name={size} value={props.units[size].unit} key={size}/>)})} 
        {props.addprod?null:!props.addsize?(<div className={Style.add} onClick={()=>{props.showAddSize()}} > 
                <svg width="12" height="12" viewBox="0 0 25 25" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <line x1="13" x2="13" y2="25" stroke="#085076"/>
                    <line y1="12" x2="25" y2="12" stroke="#085076"/>    
                </svg> 
            </div> ):<DropDown width="100px" dropChange={props.addSize} display="inline-block" values={props.allSizes}/>}
        </div>
        </div>
        <div className={Style.buttonCont}>
        {props.addprod?null:<button onClick={()=>{props.showConfirmation("Delete product",props.productDelete)}}>Delete Product</button>}
        <button onClick={props.productSaveChange}>Save Changes</button>
        </div>
        
    </div>
    )
    return(
        <Fragment>
            {(props.product.barcode===null && !props.addprod)?null:details}
        </Fragment>
    )
}

export default ProductDetails