import React from 'react'
import Style from './ConformationForm.module.css'
import InputForm from '../InputForm/InputForm'

const ConformationForm = (props)=>{
    return(
        <InputForm CloseModal={props.cancel}>
            <div className={Style.ConformationForm}>
                Do you want to {props.message} ?
                <div className={Style.buttonCont}>
                    <button onClick={props.cancel}>No</button>
                    <button onClick={props.proceed}>Yes</button>
                </div>
            </div>
        </InputForm>
    )
}

export default ConformationForm;