import React, {Component, Fragment} from 'react'
import OdersBar from '../../component/OrdersBar/OdersBar'
import FixedBar from '../../component/FixedBar/FixedBar'
import Style from './Orders.module.css'
import OrderDetails from '../../component/OrderDetails/OrderDetails'
import firebase from '../../Firebase/index'
import InputForm from '../../component/InputForm/InputForm'
import InputTag from '../../component/InputTag/InputTag'
class Orders extends Component{
    swap=(items, leftIndex, rightIndex)=>{
        var temp = items[leftIndex];
        items[leftIndex] = items[rightIndex];
        items[rightIndex] = temp;
    }
    partition=(items, left, right)=> {
        var pivot   = items[Math.floor((right + left) / 2)].createdAt, //middle element
            i       = left, //left pointer
            j       = right; //right pointer
        while (i <= j) {
            while (items[i].createdAt > pivot) {
                i++;
            }
            while (items[j].createdAt < pivot) {
                j--;
            }
            if (i <= j) {
                this.swap(items, i, j); //sawpping two elements
                i++;
                j--;
            }
        }
        return i;
    }
    
    quickSort = (items, left, right)=>{
        var index;
        if (items.length > 1) {
            index = this.partition(items, left, right); //index returned from partition
            if (left < index - 1) { //more elements on the left side of the pivot
                this.quickSort(items, left, index - 1);
            }
            if (index < right) { //more elements on the right side of the pivot
                this.quickSort(items, index, right);
            }
        }
        return items;
    }
    orderChangeHandler = (index)=>{
        this.setState({order:this.state.orders[index],activeOrder:index})
        console.log(this.state.order)
    }
    SearchHandler = (e)=>{
        this.setState({search:e.target.value})
    }
    upload = ()=>{
        let order = {...this.state.order}
        delete order.orderId
        firebase.db.collection('orders').doc(this.state.order.orderId).set({
            ...order
        })
        .then(()=>{
            console.log("success")
            this.setState({addShow:false,order:{products:[]},activeOrder:null})
            this.getOrders()
        })
    }
    statusChangeHandler = (status,id=null)=>{
        if(this.state.order.isOnline){
            firebase.db.collection('productInOrder').doc(id).set({
                status:status
            },{ merge: true })
            .then(()=>{
                this.getOrderProduct()
            })
        }
        else{
            firebase.db.collection('orders').doc(this.state.order.orderId).set({
                status:status
            },{ merge: true })
        }
        this.getOrders()
    }
    constructor(props){
        super(props);
        this.state = {
            orders:null,
            orderProduct:null,
            merchants:null,
            customers:{},
            order:{
                products:[],
                merchantIds:[]
            },
            activeOrder:null,
            search:'',
            addShow:false,
            filterSellerValue:"",
            filterStatusValue:""
        }
        this.getOrders()
        this.getSeller()
        this.getOrderProduct()
    }
    getOrders = ()=>{
        let customer = {}
        let orders=[]
        firebase.db.collection('orders').get()
        .then(querySnapshot => {
            querySnapshot.forEach( doc => {
                let data={
                    ...doc.data(),
                    orderId:doc.id
                }
              orders.push(data)
              firebase.db.collection('User').doc(doc.data().userId).get()
              .then(userDoc =>{
                customer[userDoc.id] = userDoc.data()
                this.setState({customers:customer})
              })
            })
            this.quickSort(orders,0,orders.length-1)
            this.setState({orders:orders})
            if(this.state.activeOrder!==null){
                this.orderChangeHandler(this.state.activeOrder)
            }
          })
    }
    getOrderProduct = ()=>{
        let orderProduct = {}
        firebase.db.collection('productInOrder').get()
        .then(querySnapshot =>{
            querySnapshot.forEach(doc=>{
                orderProduct[doc.id] = doc.data()
            })
            this.setState({orderProduct:orderProduct})
        })
    }
    inputChangeHandler = (e,tag)=>{
        let orderCopy={
            ...this.state.order
        }
        console.log(orderCopy)
        orderCopy[tag] = e.target.value
        this.setState({order:orderCopy})
    }
    modifyDelivery = ()=>{
        this.setState({addShow:!this.state.addShow})
    }
    getSeller = ()=>{
        firebase.db.collection('merchant').get()
        .then(
            querySnapshot =>{
                let merchants={}
                let merchantList = [{name:"Filter Seller",value:""}]
                querySnapshot.forEach(doc=>{
                    merchants[doc.id]=doc.data().storeName
                    merchantList.push({
                        name:doc.data().storeName,
                        value:doc.id
                    })
                })
                this.setState({merchants:merchants,merchantList:merchantList})
            }
        )
    }
    sellerFilterChangeHandler = (value) => {
        this.setState({filterSellerValue:value})
    } 
    statusFilterChangeHandler = (value) =>{
        this.setState({filterStatusValue:value})
    }
    render(){
        return(<Fragment>
            {this.state.orders===null||this.state.merchants===null||this.state.orderProduct===null?null:(            
            <div className={Style.Orders}>
                <OdersBar filters={["seller","orderStatus"]} filterSellerValue={this.state.filterSellerValue} filterStatusValue={this.state.filterStatusValue} statusFilterChangeHandler={this.statusFilterChangeHandler} sellerFilterChangeHandler={this.sellerFilterChangeHandler} searchHandler={this.SearchHandler} merchantList={this.state.merchantList} statusList={[{name:"Status Filter",value:""},{name:"Online Purchase",value:"Online Purchase"},{name:"Store Purchase",value:"Store Purchase"}]} searchString={this.state.search} merchants={this.state.merchants} orderProduct={this.state.orderProduct} orderChangeHandler = {this.orderChangeHandler} activeOrder={this.state.activeOrder} orders={this.state.orders} />
                <FixedBar>
                    {this.state.order.orderId?<OrderDetails modifyDelivery={this.modifyDelivery} statusChangeHandler={this.statusChangeHandler} orderProduct={this.state.orderProduct} merchants={this.state.merchants} customer={this.state.customers[this.state.order.userId]} order={this.state.order} />:null}
                </FixedBar>
            </div>
            )}
            {this.state.addShow?<InputForm CloseModal={this.modifyDelivery}><InputTag inputChange={this.inputChangeHandler} width="100%" height="80px" name="address" type="box" tagName="address" label="Delivery Address" value={this.state.order.address}/><button onClick={(e)=>{this.upload(e)}} className={Style.Button}>Submit</button></InputForm>:false}
          </Fragment>

        )
    }
}

export default Orders