import React from 'react'
import SearchBar from '../../SearchBar/SearchBar';
import Style from './ToolBar.module.css'
import DropDown from '../../DropDown/DropDown'
const ToolBar = (props)=>{
    return(
        <div className={ Style.ToolBar }>
            <div className= { Style.head } >{props.head}</div>
            <SearchBar searchHandler={props.searchHandler} searchString={props.searchString} placeholder={props.placeholder} />
            {props.head==="Products"||props.head==="Sellers"?<button className={Style.button} onClick={()=>{props.newProductHandler()}}>
            <svg width="11" height="11" viewBox="0 0 25 25" fill="none" xmlns="http://www.w3.org/2000/svg">
                <line x1="13" x2="13" y2="25" stroke="#085076"/>
                <line y1="12" x2="25" y2="12" stroke="#085076"/>
            </svg>
            </button>:null}
            <div className={ Style.dropdowns }>
                {props.filters?props.filters.includes("seller")?<DropDown values={props.merchantList} dropChange={props.sellerDropChange} border margin="0" width="60%" full/>:<div>filter seller</div>:<div>filter seller</div>}
                {props.filters?props.filters.includes("category")?<DropDown values={props.categoryList} dropChange={props.categoryDropChange} border margin="0" width="60%" full/>:<div>filter category</div>:<div>filter category</div>}
                {props.filters?props.filters.includes("orderStatus")?<DropDown values={props.statusList} dropChange={props.statusDropChange} border margin="0" width="60%" full/>:null:null}
            </div>
        </div>
    );
}

export default ToolBar