import React from 'react';
import Style from './InputTag.module.css'
function InputTag(props) {
    let styled = {
        width:props.width,
        height:props.height,
        marginRight:props.margin?props.margin:null
    }
    let inp= null
    const selectChange = (e)=>{
        if(e.target.value==="delete"){
            props.deleteCategoryHandler()
        }
        else if(e.target.value==="front"){
            props.moveCategoryHandler("front")
        }
        else if(e.target.value==="back"){
            props.moveCategoryHandler("back")
        }
    }
    if(props.type==="box"){
        inp = <textarea name={props.name} placeholder={props.name} onChange={(e)=>{props.inputChange(e,props.tagName)}} value={props.value} />
    }
    else{
        inp = <input type="text" name={props.name} placeholder={props.name} onChange={(e)=>{props.inputChange(e,props.tagName)}} value={props.value} />
    }
  return (
      <div className={ Style.InputTag } style={styled}>
          <label htmlFor={props.name}>{props.label}</label>
          {inp}
          {props.moveCategoryHandler?<select onChange={selectChange}>
              <option value="">...</option>
              <option value="delete">delete</option>
              <option value="front">Move front</option>
              <option value="back">Move Back</option> 
          </select>:null}
      </div>
  );
}

export default InputTag;
